import {mapState} from 'vuex'
export default {
  data(){
    return {
      day_status: [
        {
          label: "",
          open: true
        }, {
          label: "",
          open: true
        }, {
          label: "",
          open: false
        }, {
          label: "",
          open: true
        }, {
          label: "",
          open: false
        }, {
          label: "",
          open: false
        }, {
          label: "",
          open: false
        }
      ],
      equipments: [
        {
          name: "MDX40A",
          cover: "/static/img/equipments_maker/MDX40A.jpg"
        },
        {
          name: "rep2x",
          cover: "/static/img/equipments_maker/rep2x.png"
        },
        {
          name: "replicator",
          cover: "/static/img/equipments_maker/replicator_default.png"
        },
        {
          name: "Universal VLS",
          cover: "/static/img/equipments_maker/Universal VLS 6.60.jpg"
        },
        {
          name: "Up Box",
          cover: "/static/img/equipments_maker/Box.png"
        },
        {
          name: "",
          cover: "/static/img/equipments_maker/.jpg"
        },
        {
          name: "",
          cover: "/static/img/equipments_maker/.jpg"
        },
        {
          name: "",
          cover: "/static/img/equipments_maker/.png"
        }
      ]
    }
  },
  computed:{
    ...mapState(['scrollTop']),
    status_text(){
      return this.day_status.filter(o=>o.open).map(t=>""+t.label).join(" / ")
    }
  }
}



