import {mapState} from 'vuex'
import $ from 'jquery'
// import courseData from '../Data/courseDatas.js'
export default {
  data(){
    return {
      wishing: false,
      wishFinish: false,
      wishContent: "",
      // courseData,
      catas: [
        {
          img: "/static/img/course_cata_1.svg",
          code: "DS5100",
          title: "",
          description: ",:"
        },
        {
          img: "/static/img/course_cata_2.svg",
          code: "DS5200",
          title: "",
          description: ",,: 3DAPP",
        },
        {
          img: "/static/img/course_cata_3.svg",
          code: "DS5300",
          title: "",
          description: ",,:",
        },
        {
          img: "/static/img/course_cata_4.svg",
          code: "DS5400",
          title: "",
          description: ",,",
        }
      ],
      abilities: [
        {
          label: "Mindset",
          description: "T"
        },
        {
          label: "Make",
          description: ""
        },
        {
          label: "Communication",
          description: ""
        },
        {
          label: "Creativity",
          description: ""
        },
        {
          label: "Analysis",
          description: ""
        },
        {
          label: "Storytelling",
          description: ""
        }
      ]
    }
  },
  computed:{
    ...mapState(['courses','scrollTop']),
    sortedCourses(){
      return this.courses.sort((a,b)=>{
        return a.order_id>b.order_id?1:-1
      })
    },
    zPan(){
      return -( this.scrollTop / $("html").height())*window.innerHeight/1.5+window.innerHeight/2+50
    }
  },
  methods: {
    sendWish(){
      axios.post("/api/course/wish",{
        content: this.wishContent
      }).then(()=>{
        this.wishFinish=true
      })
    }
  }
}



