import {mapState} from 'vuex'
import vue_lazy_table from '../Data/vue_lazy_table'
//import ActivityInfoRow from './ActivityInfoRow'
import _ from 'lodash'
export default {
    
    data(){
      return {
        order: true,
        tableRows: [
          "subtitle -> __hide",
          "description -> __hide",
          "cover -> __hide",
          " -> __hide",
          "tags -> __hide",
          "link -> __hide",
          "content -> __hide",
          "id -> #",
          "title -> ",
          "authos -> ",
          "cata -> ",
          "date -> ",
          "show -> ",
          "stick -> ",
          "created_at -> __hide",
          "updated_at -> "
        ],
        btns: [
          {
            label: "",
            action: this.editrow,
            class: function(){},
          }
        ]
      }
    },
    mounted() {
        console.log('Component mounted.')
       
    },
    methods: {
      editrow(row){        
        this.$router.push("/manage/post/"+row.id)
      }
    },
    computed:{
      ...mapState(['user','activities','posts']),
      OrderedList(){
        let result = this.posts.slice().sort((a,b)=>new Date(a.time)>new Date(b.time) )
        
        if (this.order){
          result=result.reverse()
        }
        //console.log(result.map(o=>o.time))
        return result
      },
      chunkedList(){
        //
        let result =  _.groupBy(
          this.OrderedList,event=>
            (new Date(event.time )).getFullYear()*100+(new Date(event.time )).getMonth()
        )
        console.log(result)

        //
        let resultArray = []
        let keys = Object.keys(result)
        if (keys){
          keys.forEach(key => {
                  console.log(key)
                  resultArray.push( {
                  'time':key, 
                  'events':result[key]
                  })
          })
          
        }
        if (this.order){
          resultArray=resultArray.reverse()
        }
        return resultArray
      }
    },
    components:{
     
      vue_lazy_table
    
    }
}



