import {mapState} from 'vuex'
import vue_lazy_table from '../Data/vue_lazy_table'
export default {
    data() {
      return {
        lists: [],
        event: {},
        keyword: "",
        tableRows: [
          "uuid -> __hide",
          "name -> ",
          "type -> ",
          "created_at -> ",
          "updated_at -> __hide",
          "verified -> ",
          "student_id -> ",
          "school -> ",
          "department -> ",
          "agency -> ",
          "phone -> ",
          "email -> ",
          "status -> ",
          "time -> ",
          "admingroup -> ",
        ]

      }
    },
    props: ['event_id'],
    mounted() {

        //
        axios.get(`/api/user/list`,{
          params:{
            token: this.auth.token
          }
        }).then((res)=>{
          Vue.set(this,"lists",res.data);
        })
    },
    computed:{
      ...mapState(['user','auth']),
      userList(){
        let r = JSON.parse(JSON.stringify(this.lists))
        r.forEach(user=>user.created_at = user.created_at.split(' ')[0])
        return r.filter(user=> (this.keyword=="" || JSON.stringify(user).indexOf(this.keyword)!=-1))
      }
    },
    components:{
      vue_lazy_table
    }
}



